.goBackBtn {
    margin: 0 0 0 16px;
    padding: 8px 12px;
    border-radius: 4px;
    background-color: #fff;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    text-align: center;
    display: inline-block;
    color: #3f51b5;
    border: 0;
    text-decoration: none;
    cursor: pointer;
    font-family: inherit;
    font-size: 12px;
    line-height: 24px;
    font-style: normal;
    font-weight: 500;
    max-width: 100px;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
        0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
    }
    
.goBackBtn:hover,
.goBackBtn:focus {
    background-color: #303f9f;
    color: #fff;
}
